import React, { Component } from "react";
import { Form, Input, Button, Row, Col, message } from "antd";
import { Link } from "react-router-dom";
import { injectIntl } from "react-intl";
import { signup } from "./../../services";
import styled from "styled-components";
import "./index.css";

const FormItem = Form.Item;

const Div = styled.div`
  display: flex;
  justify-content: center button {
    width: 50%;
  }
`;

function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class SignIn extends Component {
  state = {
    plan: "annual",
    loading: false
  };

  _changePlan = plan => {
    this.setState({ plan });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSubmit = e => {
    const {
      intl: { messages }
    } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const form = {
          fullName: values.fullName,
          email: values.email,
          password: values.password
        };
        this.setState({
          loading: true
        });
        signup(form)
          .then(response => {
            message.success(messages.signup.success);
            this.setState({
              loading: false
            });
            this.handleReset();
          })
          .catch(error => {
            if (error.response.status === 400) {
              message.error(messages.signup.error.email);
            } else {
              message.error(messages.signup.error.all);
            }

            this.setState({
              loading: false
            });
          });
      }
    });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue("password")) {
      callback("Two passwords that you enter is inconsistent!");
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && this.state.confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  render() {
    const { getFieldDecorator, getFieldsError } = this.props.form;
    const { loading } = this.state;

    return (
      <div>
        <img
          style={{ width: "100%" }}
          alt="logo"
          src="/images/PangeaMTsmall.png"
        />
        <Form onSubmit={this.handleSubmit} className="login-form">
          <FormItem hasFeedback>
            {getFieldDecorator("fullName", {
              rules: [
                {
                  required: true,
                  message: "Please, fullName is required!"
                }
              ]
            })(<Input placeholder="FullName" type="text" />)}
          </FormItem>
          <FormItem hasFeedback>
            {getFieldDecorator("email", {
              rules: [
                {
                  type: "email",
                  message: "The input is not valid E-mail!"
                },
                {
                  required: true,
                  message: "Please input your E-mail!"
                }
              ]
            })(<Input placeholder="E-mail" />)}
          </FormItem>
          <FormItem hasFeedback>
            {getFieldDecorator("password", {
              rules: [
                {
                  required: true,
                  message: "Please input your password!"
                },
                {
                  validator: this.validateToNextPassword
                }
              ]
            })(<Input placeholder="Password" type="password" />)}
          </FormItem>
          <FormItem hasFeedback>
            {getFieldDecorator("confirm", {
              rules: [
                {
                  required: true,
                  message: "Please confirm your password!"
                },
                {
                  validator: this.compareToFirstPassword
                }
              ]
            })(
              <Input
                type="password"
                placeholder="Confirm Password"
                onBlur={this.handleConfirmBlur}
              />
            )}
          </FormItem>
          <FormItem>
            <Div>
              <Button
                type="primary"
                htmlType="submit"
                loading={loading}
                className="login-form-button"
                disabled={hasErrors(getFieldsError())}
              >
                {!loading ? "Register" : "Send Data"}
              </Button>
            </Div>
          </FormItem>
          <FormItem>
            <Div style={{ marginTop: -10 }}>
              Or &nbsp;&nbsp;<Link to="/signin">log in now!</Link>
            </Div>
          </FormItem>
        </Form>
      </div>
    );
  }
}

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default WrappedSignIn;
