import React, { Component } from "react";
import { Row, Col, Card, Icon, Button, Spin } from "antd";

import { getProcessByUuid } from "./../../services";

export default class DownloadCmp extends Component {
  state = {
    loading: false,
    process: {
      fileName: null
    }
  };

  componentDidMount() {
    const { uuid } = this.props.match.params;
    this.setState({
      loading: true
    });
    getProcessByUuid(uuid)
      .then(result => {
        console.log(result);
        this.setState({
          process: result.data,
          loading: false
        });
      })
      .catch(err => {
        this.setState({
          loading: false
        });
      });
  }

  download = record => {
    const { process } = this.state;
    var base64 = process.fileDownload.file;
    var type = process.fileDownload.fileType;
    var name = process.fileDownload.filename;
    window
      .FileDownload(base64, type, name)
      .then(() => console.log("download successful."))
      .catch(err => console.error("download error: ", err));
    
    window.location="/"  
  };

  render() {
    return (
      <Row type="flex" justify="space-around" align="middle">
        <Col xs={24} md={10}>
          <Spin spinning={this.state.loading} tip="Loading...">
            <Card style={{ margin: 100 }} title="Download File">
              <Row>
                <Col>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                    {this.state.process.fileName
                      ? this.state.process.fileName
                      : ""}
                  </div>
                </Col>
              </Row>
              <Row>
                <Col>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                    <Button
                      onClick={() => {
                        this.download();
                      }}
                      type="primary"
                      shape="round"
                      icon="download"
                      size="large"
                    >
                      Download
                    </Button>
                  </div>
                </Col>
              </Row>
            </Card>
          </Spin>
        </Col>
      </Row>
    );
  }
}
