import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import {
  Input,
  Breadcrumb,
  Table,
  Tabs,
  Button,
  message,
  Popconfirm,
  Modal,
  Row,
  Col,
  Form,
  Select,
  Radio
} from "antd";
import styled from "styled-components";
import { injectIntl } from "react-intl";
import {
  listAllUsers,
  createUser,
  updateUser,
  listTypeOfPermits,
  removeUser
} from "./../../services";

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

class UsersCmp extends Component {
  state = {
    users: [],
    typeOfPermits: [],
    defaultTypeOfPermits: null,
    user: {
      fullName: "",
      rol: "user",
      email: "",
      typeOfUser: 3
    },
    loading: false,
    sendLoading: false,
    visible: false,
    visibleEdit: false,
    mode: "add"
  };

  componentWillMount = () => {
    this.getUsers();
    this.getTypeOfPermits();
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  showModal = (mode, user = null) => {
    const { userLogin } = this.props;
    if (mode === "edit") {
      this.setState({
        visibleEdit: true,
        mode,
        user,
        defaultTypeOfPermits: user.TypeOfPermit ? user.TypeOfPermit.id : null
      });
    } else {
      if (userLogin.rol === "client") {
        this.setState({
          visible: true,
          mode,
          user: {
            fullName: "",
            rol: "user",
            email: "",
            typeOfUser: userLogin.typeOfUser
          },
          defaultTypeOfPermits: userLogin.TypeOfPermit
            ? userLogin.TypeOfPermit.id
            : null
        });
      } else {
        this.setState({
          visible: true,
          mode,
          user: {
            fullName: "",
            rol: "user",
            email: "",
            typeOfUser: 3
          }
        });
      }
    }
  };

  hideModal = () => {
    const self = this;
    this.setState({
      visible: false,
      visibleEdit: false
    });
    this.handleReset();
    setTimeout(() => {
      self.setState({
        mode: "add"
      });
    }, 1000);
  };

  getUsers = async () => {
    try {
      this.setState({
        loading: true
      });
      const response = await listAllUsers();
      this.setState({
        users: response.data.users,
        loading: false
      });
    } catch (err) {
      message.error(err.message);
      this.setState({
        loading: false
      });
    }
  };

  getTypeOfPermits = async () => {
    try {
      this.setState({
        loading: true
      });
      const response = await listTypeOfPermits();
      let defaultTypeOfPermits = null;

      if (this.props.userLogin.rol !== "client") {
        for (let index = 0; index < response.data.length; index++) {
          if (response.data[index].default) {
            defaultTypeOfPermits = response.data[index].id;
            break;
          }
        }
        this.setState({
          typeOfPermits: response.data,
          defaultTypeOfPermits,
          loading: false
        });
      } else {
        this.setState({
          typeOfPermits: response.data,
          loading: false
        });
      }
    } catch (err) {
      message.error(err.message);
      this.setState({
        loading: false
      });
    }
  };

  mustPay = value => {
    if (value) {
      let user = this.state.user;
      user.quotes = true;
      this.setState({ user });
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSubmit = e => {
    const {
      intl: { messages }
    } = this.props;
    const { mode, user } = this.state;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err || (mode === "edit" && err.confirm && err.password)) {
        const form = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: values.rol,
          typeOfPermits: values.typeOfPermits,
          typeOfUser: values.typeOfUser
        };
        this.setState({
          sendLoading: true
        });

        if (mode === "add") {
          createUser(form)
            .then(response => {
              message.success("Action successfully.");
              this.setState({
                sendLoading: false
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }

              this.setState({
                sendLoading: false
              });
            });
        } else {
          updateUser(form)
            .then(response => {
              message.success("Action successfully.");
              this.setState({
                sendLoading: false
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }

              this.setState({
                sendLoading: false
              });
            });
        }
      }
    });
  };

  confirm = async id => {
    try {
      await removeUser({ id });
      this.getUsers();
      message.success("Action successfully.");
    } catch (err) {
      message.error(err.message);
    }
  };

  render() {
    const { userLogin } = this.props;
    const {
      user,
      typeOfPermits,
      defaultTypeOfPermits,
      users,
      loading,
      visible,
      visibleEdit,
      sendLoading,
      mode
    } = this.state;

    const { getFieldDecorator, getFieldsError } = this.props.form;

    const columns = [
      {
        title: "Full Name",
        dataIndex: "fullName",
        key: "fullName"
      },
      {
        title: "Email",
        dataIndex: "email",
        key: "email"
      },
      {
        title: "Rol",
        dataIndex: "rol",
        key: "rol"
      },
      {
        title: "Action",
        key: "action",
        render: (text, record) => (
          <span>
            <ButtonAction
              onClick={() => {
                this.showModal("edit", record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                this.confirm(record.id);
              }}
              title="Are you sure delete this User?"
              okText="Yes"
              cancelText="No"
            >
              <ButtonAction
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </span>
        )
      }
    ];

    const operations = (
      <Button
        onClick={() => {
          this.showModal("add");
        }}
        type="primary"
        icon="user-add"
      >
        Add User
      </Button>
    );

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    };
    const formItemLayoutAux = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    };
    const radioStyle = {
      display: "block",
      height: "30px",
      lineHeight: "30px"
    };

    let showPass = (
      <div>
        <FormItem label="Password" hasFeedback>
          {getFieldDecorator("password", {
            rules: [
              {
                required: true,
                message: "Please input your password!"
              },
              {
                validator: this.validateToNextPassword
              }
            ]
          })(<Input disabled={mode === "see"} type="password" />)}
        </FormItem>
        <FormItem label="Confirm your password" hasFeedback>
          {getFieldDecorator("confirm", {
            rules: [
              {
                required: true,
                message: "Please confirm your password!"
              },
              {
                validator: this.compareToFirstPassword
              }
            ]
          })(<Input type="password" onBlur={this.handleConfirmBlur} />)}
        </FormItem>
      </div>
    );

    return (
      <div>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>Users</Breadcrumb.Item>
        </Breadcrumb>
        <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
          <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
            <TabPane tab="All Users" key="all">
              <Table
                size="small"
                loading={loading}
                dataSource={users}
                columns={columns}
              />
            </TabPane>
          </Tabs>
          <Modal
            centered
            width={600}
            title="User create"
            visible={visible}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                Cancel
              </Button>,
              <Button
                key="submit"
                type="primary"
                loading={sendLoading}
                onClick={this.handleSubmit}
              >
                Create
              </Button>
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label="FullName" hasFeedback>
                    {getFieldDecorator("fullName", {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(<Input disabled={mode === "see"} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator("email", {
                      initialValue: user.email,
                      rules: [
                        {
                          type: "email",
                          message: "The input is not valid E-mail!"
                        },
                        {
                          required: true,
                          message: "Please input your E-mail!"
                        }
                      ]
                    })(<Input disabled={mode !== "add"} />)}
                  </FormItem>
                  <FormItem label="Rol" hasFeedback>
                    {getFieldDecorator("rol", {
                      initialValue: user.rol,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <Select
                        disabled={userLogin.rol === "client"}
                        defaultValue={user.rol}
                      >
                        <Option value="user">User</Option>
                        <Option value="client">Client</Option>
                        <Option value="admin">Admin</Option>
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label="Type of permits" hasFeedback>
                    {getFieldDecorator("typeOfPermits", {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <Select
                        disabled={userLogin.rol === "client"}
                        defaultValue={defaultTypeOfPermits}
                      >
                        {typeOfPermits.map(item => {
                          return <Option value={item.id}>{item.name}</Option>;
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label="Type of user">
                    {getFieldDecorator("typeOfUser", {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <RadioGroup
                        disabled={userLogin.rol === "client"}
                        onChange={this.onChange}
                        value={this.state.value}
                      >
                        <Radio style={radioStyle} value={1}>
                          No quotes and no payment.
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          With quotes and without payment.
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          With quotes and with payment.
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>

                  {showPass}
                </Col>
              </Row>
            </Form>
          </Modal>
          <Modal
            centered
            width={600}
            title="User edit"
            visible={visibleEdit}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                Cancel
              </Button>,
              <Button
                key="submit"
                type="primary"
                loading={sendLoading}
                onClick={this.handleSubmit}
              >
                Save
              </Button>
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label="FullName" hasFeedback>
                    {getFieldDecorator("fullName", {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(<Input disabled={mode === "see"} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator("email", {
                      initialValue: user.email,
                      rules: [
                        {
                          type: "email",
                          message: "The input is not valid E-mail!"
                        },
                        {
                          required: true,
                          message: "Please input your E-mail!"
                        }
                      ]
                    })(<Input disabled={mode !== "add"} />)}
                  </FormItem>
                  <FormItem label="Rol" hasFeedback>
                    {getFieldDecorator("rol", {
                      initialValue: user.rol,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <Select
                        disabled={userLogin.rol === "client"}
                        defaultValue={user.rol}
                      >
                        <Option value="user">User</Option>
                        <Option value="client">Client</Option>
                        <Option value="admin">Admin</Option>
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label="Type of Permits" hasFeedback>
                    {getFieldDecorator("typeOfPermits", {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <Select
                        disabled={userLogin.rol === "client"}
                        defaultValue={defaultTypeOfPermits}
                      >
                        {typeOfPermits.map(item => {
                          return <Option value={item.id}>{item.name}</Option>;
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label="Type of user">
                    {getFieldDecorator("typeOfUser", {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true,
                          message: "Please, fullName is required!"
                        }
                      ]
                    })(
                      <RadioGroup
                        disabled={userLogin.rol === "client"}
                        onChange={this.onChange}
                        value={this.state.value}
                      >
                        <Radio style={radioStyle} value={1}>
                          No quotes and no payment.
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          With quotes and without payment.
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          With quotes and with payment.
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </Modal>
        </div>
      </div>
    );
  }
}
const WrappedUsersCmp = Form.create()(injectIntl(UsersCmp));

export default withRouter(WrappedUsersCmp);
