import React, { Component } from 'react'
import styled from 'styled-components'
import {Card} from 'antd'
import { connect } from "react-redux";
import { Layout } from "antd";

import ContentCmp from './../../components/Content';
import HeaderCmp from './../../components/Header';
import SignUp from './../../components/SignUp';

import Background from "./../Process/background.jpg";

const Div = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 100px;
  width: 350px;
  margin: 0 auto;
  margin-top: 70px;
`

const LayoutWrapper = styled(Layout)`
  background-image: url(${Background}) !important;
  background-size: cover !important;
  background-position: center center !important;
  min-height: 100vh !important;
`;

const CardWrapper = styled(Card)`
  background: #e4edf2 !important;
  border-color: #b4d8e4 !important;
`;

class Signin extends Component {


  render() {
    const {location} = this.props;
    return (
      <LayoutWrapper>
        <HeaderCmp/>
        <ContentCmp>
         <Div>
          <CardWrapper style={{width:"100%"}}>
            <SignUp location={location}/>
          </CardWrapper>
        </Div>
        </ContentCmp>
      </LayoutWrapper>
      
    )
  }
}


Signin.propTypes = {};

function mapStateToProps(state) {
  return {
    location: state.router.location
  };
}

export default connect(
  mapStateToProps,
)(Signin);
