import React, { Component } from 'react'
import { Breadcrumb, Tabs, Row, Col, Alert, List, Switch, message, Spin, Form, Button, Input, Radio } from 'antd';

import { setEmailNotification, getConfiguration, setPaypal } from '../../services/index';

import TypeOfPermits from "./../TypeOfPermits";
const TabPane = Tabs.TabPane;

function callback(key) {
  console.log(key);
}


class Configuration extends Component {

  state = {
    email_notification: [],
    mode: 'sandbox',
    client_id: "",
    client_secret: "",
    loading: false,
    loadingPaypal: false
  }

  componentWillMount = () => {
    this._getConfiguration();
  }

  onChange = (name) => {
    const array = this.state.email_notification.map((item) => {
      if (item.name === name) {
        item.value = !item.value;
      }
      return item;
    });
    this._setEmailNotification({ email_notification: array });
    this.setState({
      email_notification: array
    })
  }

  handleSubmit = (e) => {
    e.preventDefault();
    const _this = this;
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        _this.setState({
          loadingPaypal: true
        })
        setPaypal(values).then(() => {
          message.success("This is a message of success");
          _this.setState({
            loadingPaypal: false
          })
        }).catch((err) => {
          message.error(err.message);
          _this.setState({
            loadingPaypal: false
          })
        })
      }
    });
  }

  _getConfiguration = async () => {
    try {
      this.setState({
        loading: true
      });
      let email_notification, mode, client_id, client_secret;
      const config = await getConfiguration();
      if (config && config.data && config.data.configuration) {
        email_notification = config.data.configuration.email_notification;
        mode = config.data.configuration.mode;
        client_id = config.data.configuration.client_id;
        client_secret = config.data.configuration.client_secret;
      } else {
        email_notification = [
          {
            name: 'Waiting Quoting',
            value: false
          },
          {
            name: 'Quoting',
            value: false
          },
          {
            name: 'Quote Ready',
            value: true
          },
          {
            name: 'Pending Accept/Reject',
            value: false
          },
          {
            name: 'Preprocessing',
            value: false
          },
          {
            name: 'PostProcessing',
            value: false
          },
          {
            name: 'Finished',
            value: true
          },
          {
            name: 'Downloaded',
            value: true
          },
        ];
        mode = 'sandbox';
        client_id = "";
        client_secret = "";
      }
      this.setState({
        email_notification,
        mode,
        client_id,
        client_secret,
        loading: false
      })
    }
    catch (err) {

    }
  }

  _setEmailNotification = async (email_notification) => {
    try {
      await setEmailNotification(email_notification);
      message.success("This is a message of success");
    } catch (err) {
      message.error(err.message);
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };
    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>Process / Services</Breadcrumb.Item>
        </Breadcrumb>
        <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
          <Tabs defaultActiveKey="1" onChange={callback}>
            <TabPane tab="Mail Notifications" key="1">
              <Row>
                <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                  <Spin tip="Loading..." spinning={this.state.loading}>
                    <List
                      size="small"
                      bordered
                      dataSource={this.state.email_notification}
                      renderItem={item => (<List.Item>{item.name}<Switch style={{ float: "right" }} checked={item.value}
                        onChange={() => { this.onChange(item.name) }} /></List.Item>)}
                    />
                  </Spin>
                </Col>
                <Col xs={24} md={8}>
                  <Alert
                    message="Informational Notes"
                    description="Manage notifications through the email, select which notification activate."
                    type="info"
                    showIcon
                  />
                </Col>
              </Row>
            </TabPane>
            <TabPane tab="Paypal" key="2">
              <Row>
                <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                  <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                    <Form.Item
                      label="Mode"
                    >
                      {getFieldDecorator('mode', {
                        initialValue: this.state.mode
                      })(
                        <Radio.Group>
                          <Radio value="sandbox">Sandbox</Radio>
                          <Radio value="live">Live</Radio>
                        </Radio.Group>
                      )}
                    </Form.Item>
                    <Form.Item
                      hasFeedback
                      label="Client Id"
                    >
                      {getFieldDecorator('client_id', {
                        initialValue: this.state.client_id,
                        rules: [{
                          required: true, message: 'Please input your Client Id!',
                        }],
                      })(
                        <Input type="text" />
                      )}
                    </Form.Item>
                    <Form.Item
                      hasFeedback
                      label="Client Secret"
                    >
                      {getFieldDecorator('client_secret', {
                        initialValue: this.state.client_secret,
                        rules: [{
                          required: true, message: 'Please confirm your Client Secret!',
                        }],
                      })(
                        <Input type="text" />
                      )}
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                      <Button loading={this.state.loadingPaypal} type="primary" htmlType="submit">Save</Button>
                    </Form.Item>
                  </Form>
                </Col>
                <Col xs={24} md={8}>
                  <Alert
                    message="Informational Notes"
                    description="Data of the PayPal account with which transactions are made."
                    type="info"
                    showIcon
                  />
                </Col>
              </Row>
            </TabPane>
            <TabPane tab="Type of permits" key="3">
              <Row>
                <Col xs={24} md={16} style={{ padding: '0 10px' }}>
                  <TypeOfPermits user={this.props.user} />
                </Col>
                <Col xs={24} md={8}>
                  <Alert
                    message="Informational Notes"
                    description="Management of different groups (Motors / Processes) that are assigned to different users."
                    type="info"
                    showIcon
                  />
                </Col>
              </Row>
            </TabPane>
          </Tabs>
        </div>
      </div>
    )
  }
}

const WrappedConfiguration = Form.create()(Configuration);

export default WrappedConfiguration;
