import React, { Component } from "react";
import { Menu, Icon } from "antd";
import { Link } from "react-router-dom";

export default class Menus extends Component {
  renderAdmin = pathname => {
    return (
      <Menu
        theme="dark"
        defaultSelectedKeys={[this.props.pathname]}
        mode="inline"
      >
        <Menu.Item key="/dashboard">
          <Link to="/dashboard">
            <Icon type="dashboard" />
            <span> Dashboard </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/users">
          <Link to="/dashboard/users">
            <Icon type="user" />
            <span> Users </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/profile">
          <Link to="/dashboard/profile">
            <Icon type="profile" />
            <span> Profile </span>
          </Link>
        </Menu.Item>

        <Menu.Item key="/dashboard/configuration">
          <Link to="/dashboard/configuration">
            <Icon type="setting" />
            <span> Configurations </span>
          </Link>
        </Menu.Item>
      </Menu>
    );
  };
  renderClient = pathname => {
    return (
      <Menu
        theme="dark"
        defaultSelectedKeys={[this.props.pathname]}
        mode="inline"
      >
        <Menu.Item key="/dashboard">
          <Link to="/dashboard">
            <Icon type="dashboard" />
            <span> Dashboard </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/users">
          <Link to="/dashboard/users">
            <Icon type="user" />
            <span> Users </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/profile">
          <Link to="/dashboard/profile">
            <Icon type="profile" />
            <span> Profile </span>
          </Link>
        </Menu.Item>
      </Menu>
    );
  };
  renderUser = pathname => {
    return (
      <Menu
        theme="dark"
        defaultSelectedKeys={[this.props.pathname]}
        mode="inline"
      >
        <Menu.Item key="/dashboard">
          <Link to="/dashboard">
            <Icon type="dashboard" />
            <span> Dashboard </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/process-services">
          <Link to="/dashboard/process-services">
            <Icon type="desktop" />
            <span> Services / Processes </span>
          </Link>
        </Menu.Item>
        <Menu.Item key="/dashboard/profile">
          <Link to="/dashboard/profile">
            <Icon type="profile" />
            <span> Profile </span>
          </Link>
        </Menu.Item>
      </Menu>
    );
  };
  render() {
    const { rol, pathname } = this.props;
    if (rol === "user") {
      return this.renderUser(pathname);
    }
    if (rol === "client") {
      return this.renderClient(pathname);
    }
    if (rol === "admin") {
      return this.renderAdmin(pathname);
    }
    return null;
  }
}
