import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import {
  Form,
  Breadcrumb,
  Button,
  Row,
  Col,
  Input,
  Modal,
  Tree,
  Table,
  message,
  Popconfirm
} from "antd";
import styled from "styled-components";
import { injectIntl } from "react-intl";
import {
  getApiTypeEngines,
  addTypeOfPermits,
  listTypeOfPermits,
  editTypeOfPermits,
  removeTypeOfPermits,
  defaultTypeOfPermits
} from "../../services";

const { TreeNode } = Tree;

const ButtonWrapper = styled(Button)`
  float: right;
  margin-bottom: 15px;
`;
const ButtonDefault = styled(Button)`
  background-color: #87d068 !important;
  color: #ffffff !important;
  cursor: default !important;
`;
const FormItem = styled(Form.Item)`
  .ant-form-item-required {
    float: left;
    margin-bottom: -10px;
  }
`;
const ButtonAction = styled(Button)`
  margin: 5px;
`;

const ColWrapper = styled(Col)`
  padding: 0 15px !important;
`;

class TypeOfPermitsCmp extends Component {
  state = {
    process: [],
    loading: false,
    visible: false,
    expandedKeys: [],
    autoExpandParent: true,
    checkedKeys: [],
    arrayTypeOfPermits: [],
    typeSelected: {
      id: null,
      name: "",
      typeOfProcesses: []
    }
  };

  componentWillMount = () => {
    this.getProcesses();
    this.getTypeOfProcesses();
  };

  getProcesses = async () => {
    try {
      const process = await getApiTypeEngines();
      this.setState({
        process: process.data
      });
    } catch (err) {
      console.log(err);
    }
  };

  getTypeOfProcesses = async () => {
    try {
      this.setState({
        loading: true
      });
      const result = await listTypeOfPermits();
      this.setState({
        arrayTypeOfPermits: result.data,
        loading: false
      });
    } catch (err) {
      this.setState({
        loading: false
      });
      message.error(err.message);
    }
  };

  onCheck = checkedKeys => {
    console.log("onCheck", checkedKeys);
    this.setState({ checkedKeys });
  };

  renderTreeNodes = data =>
    data.map(item => {
      return (
        <TreeNode title={item.name} key={item.id} dataRef={item}>
          {item.engines ? this.renderTreeNodes(item.engines) : null}
        </TreeNode>
      );
    });

  onExpand = expandedKeys => {
    console.log("onExpand", expandedKeys);
    // if not set autoExpandParent to false, if children expanded, parent can not collapse.
    // or, you can remove all expanded children keys.
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  showModal = (record = null) => {
    if (record) {
      this.setState({
        visible: true,
        typeSelected: record,
        checkedKeys: record.typeOfProcesses
      });
    } else {
      this.setState({
        visible: true
      });
    }
  };

  hideModal = () => {
    this.setState({
      visible: false
    });
  };

  handleSubmit = e => {
    const _this = this;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        _this.setState({ loading: true });
        if (_this.state.typeSelected.name) {
          const form = {
            id: _this.state.typeSelected.id,
            name: values.name,
            typeOfProcesses: _this.state.checkedKeys
          };
          editTypeOfPermits(form)
            .then(result => {
              message.success("Action successfully.");
              _this.setState({ loading: false, visible: false });
              _this.getTypeOfProcesses();
            })
            .catch(err => {
              message.error(err.message);
              _this.setState({ loading: false, visible: false });
            });
        } else {
          const form = {
            name: values.name,
            typeOfProcesses: _this.state.checkedKeys
          };
          addTypeOfPermits(form)
            .then(result => {
              message.success("Action successfully.");
              _this.setState({ loading: false, visible: false });
              _this.getTypeOfProcesses();
            })
            .catch(err => {
              message.error(err.message);
              _this.setState({ loading: false, visible: false });
            });
        }
      }
    });
  };

  confirm = async id => {
    try {
      await removeTypeOfPermits({ id });
      this.getTypeOfProcesses();
      message.success("Action successfully.");
    } catch (err) {
      message.error(err.message);
    }
  };

  setDefault = async id => {
    try {
      this.setState({
        loading: true
      });
      await defaultTypeOfPermits({ id });
      this.getTypeOfProcesses();
      message.success("Action successfully.");
    } catch (err) {
      message.error(err.message);
    }
  };

  renderList = () => {
    const { loading, arrayTypeOfPermits } = this.state;
    const columns = [
      {
        title: "Name",
        dataIndex: "name",
        key: "name",
        sorter: (a, b) => a.name - b.name
      },
      {
        title: "Default",
        dataIndex: "default",
        key: "default",
        render: (text, record) => (
          <span>
            {record.default ? (
              <ButtonDefault
                size="small"
                shape="circle"
                icon="check"
                loading={loading}
              />
            ) : null}
            {!record.default ? (
              <Button
                onClick={() => {
                  this.setDefault(record.id);
                }}
                type="danger"
                size="small"
                shape="circle"
                loading={loading}
              />
            ) : null}
          </span>
        )
      },
      {
        title: "Action",
        key: "action",
        fixed: "right",
        width: 200,
        render: (text, record) => (
          <span>
            <ButtonAction
              onClick={() => {
                this.showModal(record);
              }}
              disabled={record.status <= 100}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                this.confirm(record.id);
              }}
              title="Are you sure delete this Type of Permits?"
              okText="Yes"
              cancelText="No"
            >
              <ButtonAction
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </span>
        )
      }
    ];
    return (
      <Row>
        <Col>
          <Row>
            <Col>
              <ButtonWrapper
                onClick={() => {
                  this.showModal();
                }}
                type="primary"
                icon="plus-circle"
              >
                Add permission type
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <Table
                size="small"
                loading={loading}
                columns={columns}
                dataSource={arrayTypeOfPermits}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  };

  renderForm = () => {
    const {
      form: { getFieldDecorator }
    } = this.props;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 }
      }
    };

    return (
      <Row>
        <Col>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
              <Row type="flex" justify="center">
                <ColWrapper xs={24} md={12}>
                  <Row type="flex" justify="center" align="middle">
                    <Col xs={24}>
                      <FormItem label="Name">
                        {getFieldDecorator("name", {
                          initialValue: this.state.typeSelected.name,
                          rules: [
                            {
                              required: true,
                              message: "Please input name field!"
                            }
                          ]
                        })(<Input />)}
                      </FormItem>
                    </Col>
                  </Row>
                </ColWrapper>
                <ColWrapper
                  xs={24}
                  md={12}
                  style={{
                    maxHeight: 300,
                    overflow: "auto"
                  }}
                >
                  <Row type="flex" justify="center" align="middle">
                    <Col xs={24}>
                      <Tree
                        checkable
                        onExpand={this.onExpand}
                        expandedKeys={this.state.expandedKeys}
                        autoExpandParent={this.state.autoExpandParent}
                        onCheck={this.onCheck}
                        checkedKeys={this.state.checkedKeys}
                      >
                        {this.renderTreeNodes(this.state.process)}
                      </Tree>
                    </Col>
                  </Row>
                </ColWrapper>
              </Row>
            </Form>
          </Row>
        </Col>
      </Row>
    );
  };

  render() {
    const { loading, typeSelected } = this.state;
    return (
      <div>
        <div style={{ padding: '0 0px', background: "#fff", minHeight: 360 }}>
          {this.renderList()}
        </div>

        <Modal
          width={620}
          title={
            !typeSelected.id ? "Create permission type" : "Edit permission type"
          }
          visible={this.state.visible}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={[
            <Button key="back" onClick={this.hideModal}>
              Cancel
            </Button>,
            <Button
              key="submit"
              type="primary"
              loading={loading}
              onClick={this.handleSubmit}
            >
              {!typeSelected.id ? "Create" : "Save"}
            </Button>
          ]}
        >
          {this.renderForm()}
        </Modal>
      </div>
    );
  }
}
const WrappedTypeOfPermitsCmp = Form.create()(injectIntl(TypeOfPermitsCmp));

export default withRouter(WrappedTypeOfPermitsCmp);
