import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  Layout,
  Dropdown,
  Icon,
  Avatar,
  Badge,
  Button,
  notification,
  Tag,
  Menu
} from "antd";
import styled from "styled-components";
import { Switch, Route, Redirect, Link, withRouter } from "react-router-dom";
import auth from "./../../utils/auth";
import functions from "./../../utils/functions";

import * as processActions from "./../../reducers/process";
import * as authActions from "./../../reducers/auth";
import * as notificationActions from "./../../reducers/notification";
import { getApiEngines, getUser } from "./../../services";
import socketIOClient from "socket.io-client";
import config from "./../../config";

import DashboardCmp from "./../../components/Dashboard";
import ProcessServices from "./../../components/ProcessServices";
import Profile from "./../../components/Profile";
import Configuration from "./../../components/Configuration";
import Users from "./../../components/Users";
import Menus from "./../../components/Menus";

const { Header, Content, Footer, Sider } = Layout;

const Logo = styled.div`
  height: 32px;
  background: #ffffff;
  margin: 16px;
  img {
    height: 60px;
    margin-right: 16px;
  }
`;
const AvatarWrapper = styled(Avatar)`
  cursor: pointer;
  float: right;
  margin-right: 15px !important;
  margin-top: 15px !important;
  right: 40px;
`;

const BadgeWrapper = styled(Badge)`
  cursor: pointer;
  float: right;
  margin-right: 15px !important;
  margin-top: 15px !important;
  right: 50px;
`;

const LayoutWrapper = styled(Layout)`
  min-height: 100vh !important;
`;

class Dashboard extends Component {
  state = {
    collapsed: false,
    loading: false,
    count: 0
  };

  componentWillMount = () => {
    this.getEngines();
    this._getUser();
    const socket = socketIOClient(config._API_SERVER);
    socket.on("notification_dashboard", noty => {
      this.runNotification(noty);
    });
  };

  runNotification = async noty => {
    const {
      authState,
      notificationAct: { changeNotifications }
    } = this.props;

    if (noty.data.userId === authState.user.id) {
      try {
        let result = await getUser();
        changeNotifications(result.data.user.Notifications);
        this.setState({
          count: this.state.count + 1
        });

        notification["info"]({
          message: functions.getStatusByText(noty.type),
          description:
            "You are notified that you have a process with quotes ready."
        });
      }
      catch (err) {
        console.log(err);
      }
    }
  };

  _getUser = async () => {
    const self = this;

    const {
      history,
      authAct: { fetchUser },
      notificationAct: { changeNotifications }
    } = this.props;

    this.setState({
      loading: true
    });

    try {
      let result = await getUser();
      fetchUser(result.data.user);
      changeNotifications(result.data.user.Notifications);
      this.setState({
        loading: false
      });
    } catch (err) {
      auth.clearAppStorage();
      history.push("/");
      this.setState({
        loading: false
      });
    }
  };

  getEngines = () => {
    const {
      processAct: { getEngines, fetchRequest, fetchResponse }
    } = this.props;

    fetchRequest();
    getApiEngines()
      .then(response => {
        let data = response.data;
        getEngines(data);
        fetchResponse();
      })
      .catch(error => {
        console.log(error);
        fetchResponse();
      });
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({
      collapsed
    });
  };

  logout = () => {
    const { history } = this.props;
    auth.clearAppStorage();
    history.push("/");
  };

  _selectProcess = type => {
    const {
      processAct: { selectProcess }
    } = this.props;
    selectProcess(type);
  };

  _changeFileID = fileId => {
    const {
      processAct: { changeFileID }
    } = this.props;
    changeFileID(fileId);
  };

  render() {
    const {
      location: { pathname },
      process,
      authState,
      notificationState: { notifications }
    } = this.props;
    const count = notifications.length;
    const countState = this.state.count;

    const menu = (
      <Menu>
        <Menu.Item key="2">
          <Link to="/dashboard/profile">
            <Icon type="profile" /> Profile{" "}
          </Link>
        </Menu.Item>
        <Menu.Item key="3" onClick={this.logout}>
          <Icon type="logout" /> Logout
        </Menu.Item>
      </Menu>
    );

    const aux = notifications.map((item, index) => {
      let status = functions.getStatusByText(item.type);
      return (
        <Menu.Item key={`noty${index}`}>
          <Icon type="desktop" /> {item.data.fileName}{" "}
          <Tag color="#2db7f5">{status}</Tag>
        </Menu.Item>
      );
    });
    let array = [];
    let tmp = 0;
    for (let index = aux.length - 1; index >= 0; index--) {
      array.push(aux[index]);
      tmp++;

      if (tmp === 5) break;
    }
    array.push(
      <Menu.Item
        key={`more`}
        style={{
          textAlign: "right",
          textDecoration: "underline",
          color: "#008dff",
          marginRight: 10
        }}
      >
        See more...
      </Menu.Item>
    );

    const menuNoty = <Menu>{array}</Menu>;

    return (
      <LayoutWrapper>
        <Sider
          collapsible
          collapsed={this.state.collapsed}
          onCollapse={this.onCollapse}
        >
          <Logo> </Logo>
          <Menus pathname={pathname} rol={authState.user.rol} />
        </Sider>
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: 0
            }}
          >
            <Dropdown overlay={menu} placement="bottomRight">
              <AvatarWrapper icon="user" />
            </Dropdown>{" "}
            <Dropdown overlay={menuNoty} placement="bottomRight">
              <BadgeWrapper count={count}>
                <Button shape="circle" icon="notification" />
              </BadgeWrapper>
            </Dropdown>{" "}
          </Header>{" "}
          <Content
            style={{
              margin: "0 16px"
            }}
          >
            <Switch>
              <Route
                exact
                path="/dashboard"
                render={() => <DashboardCmp user={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/users"
                render={() => <Users userLogin={authState.user} />}
              />
              <Route
                exact
                path="/dashboard/process-services"
                render={() => (
                  <ProcessServices
                    typeOfUser={authState.user.typeOfUser}
                    count={countState}
                    changeFileID={this._changeFileID}
                    process={process}
                    selectProcess={this._selectProcess}
                  />
                )}
              />
              <Route exact path="/dashboard/profile" component={Profile} />{" "}
              <Route
                exact
                path="/dashboard/configuration"
                render={() => <Configuration user={authState.user} />}
              />
              <Redirect
                to={{
                  state: {
                    error: true
                  }
                }}
              />{" "}
            </Switch>{" "}
          </Content>{" "}
          <Footer
            style={{
              textAlign: "center"
            }}
          >
            PangeaMT© 2019. All rights reserved.{" "}
          </Footer>
        </Layout>
      </LayoutWrapper>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
    notificationAct: bindActionCreators(notificationActions, dispatch),
    authAct: bindActionCreators(authActions, dispatch)
  };
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
    authState: state.rootReducer.auth,
    notificationState: state.rootReducer.notification,
    location: state.router.location
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Dashboard));
