import React, { Component } from "react";
import { Form, Icon, Input, Button, Checkbox, message } from "antd";
import { Link, withRouter } from "react-router-dom";
import { injectIntl } from "react-intl";
import { signin, setAuthToken } from "./../../services";
import styled from "styled-components";

import auth from "./../../utils/auth";
import "./index.css";

const FormItem = styled(Form.Item)`
  &.ant-form-inline .ant-form-item {
    margin: 0 !important;
  }
`;

class SignIn extends Component {
  state = { loading: false };

  handleReset = () => {
    this.props.form.resetFields();
  };

  success = response => {
    const {
      intl: { messages },
      signIn,
      history
    } = this.props;
    const { accessToken } = response.data;

    message.success(messages.signin.success);
    auth.setToken(accessToken, true);
    signIn(response.data);
    setAuthToken(accessToken);
    this.handleReset();
    history.push("/dashboard");
    this.setState({
      loading: false
    });
  };

  handleSubmit = e => {
    const {
      intl: { messages }
    } = this.props;
    const _this = this;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const form = {
          email: values.email,
          password: values.password
        };
        this.setState({
          loading: true
        });
        signin(form)
          .then(response => {
            _this.success(response);
          })
          .catch(error => {
            try {
              if (error.response.status === 404) {
                message.error(messages.signin.error.user);
              } else if (error.response.status === 401) {
                message.error(messages.signin.error.password);
              } else {
                message.error(messages.signin.error.all);
              }
              this.setState({
                loading: false
              });
            } catch (err) {
              console.log(err);
            }
          });
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.state;
    return (
      <div>
        <img
          style={{ width: "100%" }}
          alt="logo"
          src="/images/PangeaMTsmall.png"
        />
        <Form onSubmit={this.handleSubmit} className="login-form">
          <FormItem>
            {getFieldDecorator("email", {
              rules: [
                {
                  type: "email",
                  message: "The input is not valid E-mail!"
                },
                {
                  required: true,
                  message: "Please input your E-mail!"
                }
              ]
            })(
              <Input
                prefix={
                  <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder="Email"
              />
            )}
          </FormItem>
          <FormItem>
            {getFieldDecorator("password", {
              rules: [
                { required: true, message: "Please input your Password!" }
              ]
            })(
              <Input
                prefix={
                  <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                type="password"
                placeholder="Password"
              />
            )}
          </FormItem>
          <FormItem>
            {getFieldDecorator("remember", {
              valuePropName: "checked",
              initialValue: true
            })(<Checkbox>Remember me</Checkbox>)}
            <a className="login-form-forgot" href="">
              Forgot password
            </a>
            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
              loading={loading}
            >
              {!loading ? "Sign me in" : "Send Data"}
            </Button>
            Or <Link to="/signup">register now!</Link>
          </FormItem>
        </Form>
      </div>
    );
  }
}

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default withRouter(WrappedSignIn);
