import React, { Component } from "react";
import {
  Breadcrumb,
  Table,
  Row,
  Col,
  Button,
  Upload,
  Icon,
  Form,
  message,
  Select,
  Divider,
  Tag,
  Alert,
  Tooltip,
  Modal,
  Statistic,
  Tabs,
  Badge
} from "antd";

import styled from "styled-components";
import functions from "./../../utils/functions";
import {
  processFile,
  getProcess,
  quoteFile,
  processFileAfterQuoteFile
} from "./../../services";
import ImageLoader from "react-loading-image";
import "./index.css";

const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;
const TabPane = Tabs.TabPane;
require("./file-download");

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: rgb(24, 144, 255);
    }
  }
`;

const ButtonWrapper = styled(Button)`
  float: right;
  margin: 0 0 15px 0;
`;

const ButtonAction = styled(Button)`
  margin: 5px;
`;
const BadgeWrapper = styled(Badge)`
  .ant-badge-count {
    top: -5px;
    right: -10px;
  }
`;

const Div = styled.div`
  width: 100%;
  height: 170px;
  margin-top: 25px;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;

const Span = styled.span`
  // text-transform: uppercase;
  // text-decoration: underline;
`;

const DivError = styled.div`
  h2,
  p {
    text-align: center;
  }
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  width: 200px;
  height: 200px;
`;

const ColWrapper = styled(Col)`
  padding: 0 15px !important;
`;

const DivImg = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
`;

const SuccessCmp = () => (
  <Row justify="center" type="flex">
    <Col>
      <DivImg>
        <IL
          src="/images/undraw_confirmation_2uy0.svg"
          loading={() => <div>Loading...</div>}
          error={() => <div>Error</div>}
        />
      </DivImg>
      <h2 style={{ textAlign: "center" }}> You’re done!</h2>
      <p style={{ textAlign: "center" }}>
        Soon you will receive a notification to the mail to notify the
        completion of the process.
      </p>
      <Divider />
    </Col>
  </Row>
);
const ErrorCmp = () => (
  <div>
    <DivImg>
      <IL
        src="/images/undraw_bug_fixing_oc7a.svg"
        loading={() => <div>Loading...</div>}
        error={() => <div>Error</div>}
      />
    </DivImg>

    <h2> Hmm, something went wrong</h2>
    <p>so it looks like your transfer didn't work please give it another go</p>
    <Divider />
  </div>
);

class ProcessServicesCmp extends Component {
  state = {
    view: "list",
    fileList: [],
    uploading: false,
    processEnd: false,
    showError: false,
    source: null,
    target: null,
    visible: false,
    viewNoty: "process",
    value: undefined,
    _input: null,
    processData: [],
    loading: false,
    loadingPay: false,
    nameEng: null,
    processSelected: null,
    quote: null,
    payResponse: null
  };

  componentWillReceiveProps = nextProps => {
    if (this.props.count < nextProps.count) {
      this.getProcess();
    }
  };

  initStateView = view => {
    this.setState({
      view: view,
      fileList: [],
      uploading: false,
      processEnd: false,
      showError: false,
      source: null,
      target: null,
      visible: false,
      viewNoty: "process",
      value: undefined,
      _input: null,
      loading: false,
      nameEng: null,
      processSelected: null,
      quote: null
    });
  };

  componentDidMount = () => {
    // this.state._input.focus();

    this.getProcess();
  };

  changeView = view => {
    if (view === "list") {
      this.getProcess();
    }
    this.setState({
      view
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const self = this;
    const { fileList, target } = this.state;
    const {
      typeOfUser,
      changeFileID,
      process: { selected }
    } = this.props;

    const targets = this.getTargets();

    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];
    if (nameEng) {
      this.setState({
        target,
        nameEng
      });
    } else {
      alert("error");
    }

    self.setState({
      showError: false,
      viewNoty: "process"
    });
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (this.state.fileList.length) {
          functions.getBase64(fileList[0], function (err, result) {
            if (!err) {
              let aux = values.target.split("-");
              let form = {
                process: {
                  id: aux[0],
                  name: selected.name
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name
                },
                file: result
              };
              self.setState({
                uploading: true
              });
              if (typeOfUser === 2 || typeOfUser === 3) {
                quoteFile(form)
                  .then(response => {
                    message.success("This is a message of success");
                    self.setState({
                      uploading: false,
                      viewNoty: "success",
                      fileList: []
                    });

                    changeFileID(response.data.fileId);
                  })
                  .catch(error => {
                    console.log(error);
                    message.error(error.message);

                    self.setState({
                      uploading: false,
                      viewNoty: "error"
                    });
                  });
              } else {
                processFile(form)
                  .then(response => {
                    message.success("This is a message of success");
                    self.setState({
                      uploading: false,
                      viewNoty: "success",
                      fileList: []
                    });

                    changeFileID(response.data.fileId);
                  })
                  .catch(error => {
                    console.log(error);
                    message.error(error.message);

                    self.setState({
                      uploading: false,
                      viewNoty: "error"
                    });
                  });
              }
            }
          });
        } else {
          self.setState({
            showError: true
          });
        }
      }
    });
  };

  handleChange = value => {
    const {
      selectProcess,
      process: { engines }
    } = this.props;
    engines.forEach(element => {
      if (element.id.toString() === value) {
        selectProcess(element);
      }
    });
    this.setState({ value });
  };

  changeSource = source => {
    this.setState({
      source
    });
  };

  changeTarget = target => {
    this.setState({
      target
    });
  };

  getSources = () => {
    const {
      process: {
        selected: { engines }
      }
    } = this.props;
    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.source) {
          flag = true;
          break;
        }
      }
      if (!flag) {
        let key = element.source;
        array.push({ key, name: functions.languages[key] });
      }
    });
    return array;
  };

  getProcess = () => {
    const _this = this;

    _this.setState({
      loading: true
    });
    getProcess()
      .then(response => {
        _this.setState({
          processData: response.data.process,
          loading: false
        });
      })
      .catch(err => {
        message.error(err.message);
        _this.setState({
          loading: false
        });
      });
  };

  getTargets = () => {
    const { source } = this.state;
    const {
      process: {
        selected: { engines }
      }
    } = this.props;

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (source) {
        if (!flag && element.source === source) {
          if (element.domain) {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
              } - ${element.domain}`;
          } else {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
              }`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  showModal = processSelected => {
    this.setState({
      visible: true,
      processSelected
    });
  };

  hideModal = () => {
    this.setState({
      visible: false,
      payResponse: null
    });
  };

  download = record => {
    console.log(record);
    var base64 = record.fileDownload.file;
    var type = record.fileDownload.fileType;
    var name = record.fileDownload.filename;
    window
      .FileDownload(base64, type, name)
      .then(() => console.log("download successful."))
      .catch(err => console.error("download error: ", err));
  };

  pay = async (fileId, quote) => {
    this.setState({ loadingPay: true, quote: quote });
    try {
      let result = await processFileAfterQuoteFile({ fileId, quote });
      this.setState({ loadingPay: false, payResponse: "success" });
      this.getProcess();
    } catch (err) {
      this.setState({ loadingPay: false, payResponse: "error" });
      console.log(err);
    }
  };

  renderList = () => {
    const {
      processData,
      loading,
      processSelected,
      loadingPay,
      quote,
      payResponse
    } = this.state;
    const columns = [
      {
        title: "FileName",
        dataIndex: "fileName",
        key: "fileName",
        sorter: (a, b) => a.fileName - b.fileName
      },
      {
        title: "Process Name",
        dataIndex: "processName",
        key: "processName",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            {record.processName}
          </span>
        )
      },
      {
        title: "Language ",
        key: "language",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            <Tag style={{ cursor: "default" }} color="#108ee9">
              {record.engineSource}
            </Tag>{" "}
            <Icon type="arrow-right" />{" "}
            <Tag style={{ cursor: "default" }} color="#ff3f1b">
              {record.engineTarget}
            </Tag>
          </span>
        )
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status",
        render: status => <Span>{functions.getStatusByText(status)}</Span>,
        filters: [
          {
            text: "Waiting",
            value: "waiting"
          },
          {
            text: "Waiting Quoting",
            value: "waiting_quoting"
          },
          {
            text: "Quotes Ready",
            value: "quotes_ready"
          },
          {
            text: "Pending Accept/Reject",
            value: "pending_accept_reject"
          },
          {
            text: "Preprocessing",
            value: "preprocessing"
          },
          {
            text: "Processing",
            value: "processing"
          },
          {
            text: "PostProcessing",
            value: "postProcessing"
          }
        ],
        onFilter: (value, record) => record.status === value
      },
      {
        title: "Action",
        key: "action",
        render: (text, record) => (
          <span>
            <Tooltip placement="top" title="See Quotes">
              <ButtonAction
                onClick={() => {
                  this.showModal(record);
                }}
                disabled={record.status !== "quote_ready"}
                size="small"
                type="primary"
                shape="circle"
                icon="wallet"
              />
            </Tooltip>
          </span>
        )
      }
    ];
    const columns1 = [
      {
        title: "FileName",
        dataIndex: "fileName",
        key: "fileName",
        sorter: (a, b) => a.fileName - b.fileName
      },
      {
        title: "Process Name",
        dataIndex: "processName",
        key: "processName",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            {record.processName}
          </span>
        )
      },
      {
        title: "Language ",
        key: "language",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            <Tag color="#108ee9">{record.engineSource}</Tag>{" "}
            <Icon type="arrow-right" />{" "}
            <Tag color="#ff3f1b">{record.engineTarget}</Tag>
          </span>
        )
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status",
        render: status => <Span>{functions.getStatusByText(status)}</Span>
      },
      {
        title: "Action",
        key: "action",
        render: (text, record) => (
          <span>
            <Tooltip placement="top" title="Download">
              <ButtonAction
                onClick={() => {
                  this.download(record);
                }}
                disabled={record.status <= 100}
                size="small"
                type="primary"
                shape="circle"
                icon="download"
              />
            </Tooltip>
          </span>
        )
      }
    ];

    let finished=[], inProcess=[];

    if (processData && processData.length) {
      finished = processData.filter(item => {
        return item.status === "downloaded";
      });

      inProcess = processData.filter(item => {
        return item.status !== "downloaded";
      });
    }



    const operations = (
      <ButtonWrapper
        type="primary"
        icon="file-add"
        onClick={() => {
          this.changeView("process");
        }}
      >
        New Process
      </ButtonWrapper>
    );

    return (
      <div>
        <Row>
          <Col>
            <Tabs defaultActiveKey="1" tabBarExtraContent={operations}>
              <TabPane
                tab={
                  <BadgeWrapper count={inProcess.length}>
                    <span>In Process</span>
                  </BadgeWrapper>
                }
                key="1"
              >
                <Table
                  size="small"
                  loading={loading}
                  columns={columns}
                  dataSource={inProcess}
                />
              </TabPane>
              <TabPane
                tab={
                  <BadgeWrapper count={finished.length}>
                    <span>Finished</span>
                  </BadgeWrapper>
                }
                key="2"
              >
                <Table
                  size="small"
                  loading={loading}
                  columns={columns1}
                  dataSource={finished}
                />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
        <Modal
          title="Quotes"
          width={600}
          visible={this.state.visible}
          onCancel={this.hideModal}
          footer={null}
        >
          {payResponse !== "success" && payResponse !== "error" ? (
            <Row justify="center" type="flex">
              {processSelected && processSelected.quotes
                ? processSelected.quotes.map(item => {
                  return (
                    <Col key={item.optionid} xs={24} md={8}>
                      <Row justify="center" type="flex" align="middle">
                        <Col style={{ textAlign: "center", minHeight: 45 }}>
                          <Statistic
                            title={item.option}
                            value={item.price}
                            precision={4}
                          />

                          <Button
                            loading={
                              loadingPay && quote.optionid === item.optionid
                            }
                            onClick={() => {
                              this.pay(processSelected.fileId, item);
                            }}
                            style={{ marginTop: 16 }}
                            type="primary"
                          >
                            Pay
                            </Button>
                        </Col>
                      </Row>
                    </Col>
                  );
                })
                : null}
            </Row>
          ) : null}
          {payResponse === "success" ? <SuccessCmp /> : null}
          {payResponse === "error" ? <SuccessCmp /> : null}
        </Modal>
      </div>
    );
  };

  renderProcess = () => {
    const {
      process,
      form: { getFieldDecorator, getFieldsError }
    } = this.props;

    const {
      fileList,
      uploading,
      processEnd,
      showError,
      source,
      viewNoty,
      target
    } = this.state;

    const props = {
      onRemove: file => {
        if (!processEnd) {
          this.setState(state => {
            const index = state.fileList.indexOf(file);
            const newFileList = state.fileList.slice();
            newFileList.splice(index, 1);
            return {
              fileList: newFileList
            };
          });
        }
      },
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!functions.checkFile(file)) {
          message.error("You can only upload (docx|xlsx|pptx|odt) file!");
        } else if (!isLt2M) {
          message.error("Image must smaller than 2MB!");
        } else {
          this.setState(state => ({
            fileList: [...state.fileList, file]
          }));
        }

        return false;
      },
      disabled: fileList.length,
      multiple: false,
      fileList
    };

    const targets = this.getTargets();
    console.log(targets);

    if (viewNoty === "process") {
      const options = process.engines.map(d => (
        <Option key={d.id}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  this.changeView("list");
                }}
              >
                List Process
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form onSubmit={this.handleSubmit}>
                <Row type="flex" justify="center">
                  <ColWrapper xs={24} md={12}>
                    <Row>
                      <Col xs={24}>
                        <FormItemWrapper>
                          <Div error={showError && !fileList.length}>
                            <Dragger {...props}>
                              <p className="ant-upload-drag-icon">
                                <Icon type="inbox" />
                              </p>
                              <p className="ant-upload-text">
                                Click or drag file to this area to upload
                              </p>
                            </Dragger>
                            {showError && !fileList.length ? (
                              <Alert
                                style={{ margin: 5 }}
                                message="You must upload a file before continuing!!"
                                type="error"
                                showIcon
                              />
                            ) : null}
                          </Div>
                        </FormItemWrapper>
                      </Col>
                    </Row>
                  </ColWrapper>
                  <ColWrapper xs={24} md={12}>
                    <Row type="flex" justify="center" align="middle">
                      <Col xs={24}>
                        <FormItemWrapper label="Process type" hasFeedback>
                          {/* {...getFieldProps('ref')}, */}
                          {getFieldDecorator("process", {
                            disabled: source === null || processEnd,
                            rules: [
                              {
                                required: true,
                                message: "Please select your Process!"
                              }
                            ]
                          })(
                            <Select
                              autofocus="true"
                              ref={c => (this.state._input = c)}
                              onChange={this.handleChange}
                            >
                              {options}
                            </Select>
                          )}
                        </FormItemWrapper>
                        <FormItemWrapper label="Source Language" hasFeedback>
                          {getFieldDecorator("source", {
                            disabled: processEnd,
                            rules: [
                              {
                                required: true,
                                message:
                                  "Please select your Language of origin!"
                              }
                            ]
                          })(
                            <Select
                              disabled={!this.getSources().length}
                              onChange={this.changeSource}
                            >
                              {this.getSources().map(element => {
                                return (
                                  <Option value={element.key}>
                                    {element.name}
                                  </Option>
                                );
                              })}
                            </Select>
                          )}
                        </FormItemWrapper>
                        <FormItemWrapper label="Target Language" hasFeedback>
                          {getFieldDecorator("target", {
                            disabled: source === null || processEnd,
                            rules: [
                              {
                                required: true,
                                message: "Please select your Exit language!"
                              }
                            ]
                          })(
                            <Select
                              onChange={this.changeTarget}
                              disabled={source === null || processEnd}
                            >
                              {targets.map(element => {
                                return (
                                  <Option value={element.key}>
                                    {element.text}
                                  </Option>
                                );
                              })}
                            </Select>
                          )}
                        </FormItemWrapper>
                      </Col>
                    </Row>
                  </ColWrapper>
                </Row>
                <Row>
                  <Col xs={24}>
                    <FormItemWrapper
                      style={{ display: "flex", justifyContent: "center" }}
                    >
                      {!processEnd ? (
                        <Button
                          icon="file"
                          type="primary"
                          htmlType="submit"
                          size="large"
                          loading={uploading}
                          disabled={
                            !this.getSources().length ||
                            !target ||
                            !source ||
                            functions.hasErrors(getFieldsError())
                          }
                        >
                          {uploading ? "Uploading" : "Start Upload"}
                        </Button>
                      ) : null}
                    </FormItemWrapper>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
        </div>
      );
    }
    if (viewNoty === "error") {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  this.changeView("list");
                }}
              >
                List Process
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <ErrorCmp />
                <DivButtons>
                  <Button
                    onClick={() => {
                      this.initStateView("process");
                    }}
                    type="primary"
                    size="large"
                  >
                    Try Again
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
    if (viewNoty === "success") {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  this.initStateView("list");
                }}
              >
                List Process
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <SuccessCmp />
                <DivButtons>
                  <Button
                    onClick={() => {
                      this.initStateView("process");
                    }}
                    type="primary"
                    size="large"
                  >
                    Send another?
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
  };

  render() {
    const { view } = this.state;
    return (
      <div>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>Process / Services</Breadcrumb.Item>
        </Breadcrumb>
        <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
          {view === "list" ? this.renderList() : this.renderProcess()}
        </div>
      </div>
    );
  }
}

const WrappedProcessServicesCmp = Form.create()(ProcessServicesCmp);

export default WrappedProcessServicesCmp;
