import { createAction, createReducer } from "redux-act";

const initialState = {
  selected: {
    name: null,
    engines:[]
  },
  loading: false,
  engines: [],
  fileId: null
};

export const fetchRequest = createAction("fetch request", loading => ({ loading }));
export const fetchResponse = createAction("fetch response", loading => ({ loading }));
export const getEngines = createAction("get engines", engines => ({ engines }));
export const selectProcess = createAction("select process", selected => ({
  selected
}));
export const changeFileID = createAction("change fileId", fileId => ({
  fileId
}));

export default createReducer(
  {
    [selectProcess]: (state, { selected }) =>
      Object.assign({}, state, { selected }),
    [getEngines]: (state, { engines }) => Object.assign({}, state, { engines }),
    [changeFileID]: (state, { fileId }) => Object.assign({}, state, { fileId }),
    [fetchRequest]: (state) => Object.assign({}, state, { loading: true }),
    [fetchResponse]: (state) => Object.assign({}, state, { loading: false})
  },
  initialState
);
